import React, { Component } from 'react';
import './App.css';
import EventList from './EventList';
import Modal from './Modal';
import CitySearch from './CitySearch';
import NumberOfEvents from './NumberOfEvents';
import { getEvents } from './api';
import { OfflineAlert } from './Alert'
import moment from 'moment';
import {ScatterChart, Scatter, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer} from 'recharts';


class App extends Component {

  componentDidMount(){
    getEvents().then(response => this.setState({ events: response }));
    window.addEventListener('online', this.connectAlert());
  }

  state = {
    events: [],
    lat: null,
    lon: null,
    page: null,
    onlineStatus:'',
    selectedEventId: '',
    modalEvent: [],
    miles: 50
  }


  connectAlert = () => {
    if(navigator.onLine === false) {
      this.setState({onlineStatus: 'You\'re currently offline!'});
    } else {
      this.setState({
        onlineStatus: '',
      })
    }
  }

  updateEvents = (lat, lon, page) => {
    if (lat && lon) {
      getEvents(lat, lon, this.state.page).then(events => this.setState({ events, lat, lon }));
    } else if (page) {
      getEvents(this.state.lat, this.state.lon, page).then(events => 
        this.setState({ events, page})
        );
    } else {
      getEvents(this.state.lat, this.state.lon, page).then(events => 
        this.setState({events}))
    }
  }

  countEventsOnADate = (date) => {
    let count = 0;
    for (let i = 0; i < this.state.events.length; i += 1) {
      if (this.state.events[i].local_date === date) {
        count += 1;
      }
    }
    return count;
  }

  getData = () => {
    const next7Days = [];
    const currentDate = moment();
    for (let i = 0; i < 7; i += 1) {
      currentDate.add(1, 'days');
      const dateString = currentDate.format('YYYY-MM-DD');
      const count = this.countEventsOnADate(dateString);
      next7Days.push({ date: dateString, number: count });
    }
    return next7Days;
  }
  openPopup = (e) => {
    document.getElementById('popup-container').style.display = 'block'
  }
  closePopup = (e) => {
    if(e.target.id === 'popup-container' || e.target.className === "mile-list-item") {
      document.getElementById('popup-container').style.display = "none"
    }
  }
  openSearchPopup = (e) => {
    document.getElementById('search-popup-container').style.display = "block"
  }
  closeSearchPopup = (e) => {
    if(e.target.id === 'search-popup-container') {
      document.getElementById('search-popup-container').style.display = 'none'
    }
  }
  changeDistance = (e) => {
    const newDist = e.target.id
    this.setState({
      miles: newDist
    })
  }

  changeSelectedEvent = (newId) => {
    this.setState({
      selectedEventId: newId
    })
    const theEvents = this.state.events
    theEvents.forEach(theEvent => {
      if(newId === theEvent.id) {
        this.setState({
          modalEvent: theEvent
        })
      }
    });
  }
  closeModal = () => {
    this.setState({
      modalEvent:[]
    })
  }
  openChart = () => {
    document.getElementById('chart-modal-container').style.display = 'block'
  }
  closeChart = () => {
    document.getElementById('chart-modal-container').style.display = 'none'
  }
  closeChartModal = (e) => {
    if(e.target.id === 'chart-modal-container') {
      document.getElementById('chart-modal-container').style.display = 'none'
    }
  }  
  render() {
    return (
        <div className="App">
          <header className='top-header'></header>
          <h1 className="next-header">Find your next event</h1>
          <p className="how-many-events">{this.state.events.length} events near you</p>
          <div className="search-area">
            <CitySearch className="city-search" updateEvents={this.updateEvents} />
            <div className="next-to-search">within <div className="miles" onClick={(e) => this.openPopup(e)}>{this.state.miles} miles</div> of <div className="city" onClick={(e) => this.openSearchPopup(e)}>München</div></div>
            <div className='chart-button' onClick={() => this.openChart()}>Events this week</div>
          </div>
          <div className="popup-container" id="popup-container" onClick={(e) => this.closePopup(e)}>
            <div className="miles-popup" id="miles-popup">
              <div className="the-triangle"></div>
                  <div id="2" className="mile-list-item" onClick={(e) => this.changeDistance(e)}>2 miles</div>
                <div id="5" className="mile-list-item" onClick={(e) => this.changeDistance(e)}>5 miles</div>
                <div id="10" className="mile-list-item" onClick={(e) => this.changeDistance(e)}>10 miles</div>
                <div id="25" className="mile-list-item" onClick={(e) => this.changeDistance(e)}>25 miles</div>
                <div id="50" className="mile-list-item" onClick={(e) => this.changeDistance(e)}>50 miles</div>
                <div id="100" className="mile-list-item" onClick={(e) => this.changeDistance(e)}>100 miles</div>
            </div>
          </div>
          <div className="search-popup-container" id="search-popup-container" onClick={(e) => this.closeSearchPopup(e)}>
          <div className="search-the-triangle"></div>
            <div className="search-popup" id="search-popup">
              <CitySearch className="city-search-popup" updateEvents={this.updateEvents} />
            </div>
          </div>
          <div className="chart-modal-container" id="chart-modal-container" onClick={(e) => this.closeChartModal(e)}>
            <div className="chart-modal">
              <div className="close-modal-x" onClick={() => this.closeChart()}>X</div>
              <h1 className="modal-title">Events per day</h1>
              <ResponsiveContainer height={400}>
                <ScatterChart className="the-chart" margin={{top: 20, right: 20, bottom: 20, left: 20,}}>
                  <CartesianGrid />
                  <XAxis type="category" dataKey="date" name="date"/>
                  <YAxis type="number" dataKey="number" name="number of events" allowDecimals={false}/>
                  <Tooltip cursor={{ strokeDasharray: '3 3' }} />
                  <Scatter data={this.getData()} fill="#8884d8" />
                </ScatterChart>
              </ResponsiveContainer>
            </div>
          </div>
          <OfflineAlert text={this.state.onlineStatus}/>
          <EventList events={this.state.events} changeSelEv={(theId) => this.changeSelectedEvent(theId)} />
          <Modal selectedModalEvent={this.state.modalEvent} selectedEventId={this.state.selectedEventId} close={() => this.closeModal()}/>
            
            <NumberOfEvents updateEvents={this.updateEvents}/>

        
      </div>
    );
  }
}

export default App;
